<template>
    <div class="alterBankCard">
        <van-nav-bar fixed :title="alterBankParams.bankconact ? '' : ''" left-text="" left-arrow @click-left="linkBack" />
        <header class="header">{{ alterBankParams.bankconact ? '' : ''}}</header>
        <section class="section">
            <van-cell-group>
                <van-field
                    v-model="alterBankParams.bankconact"
                    clearable
                    label=""
                    placeholder=""
                />

                <van-field
                    v-model="alterBankParams.bankaccount"
                    clearable
                    label=""
                    placeholder=""
                />
                <van-field
                    v-model="alterBankParams.bankname"
                    clearable
                    label=""
                    placeholder=""
                />
            </van-cell-group>
        </section>
        <footer class="footer">
            <van-button class="carryCashBtn" @click="confirmAlter"></van-button>
        </footer>
    </div>
</template>

<script>
import { reqAlterBank } from "@/api/carryCash";
import { Toast, Dialog } from "vant";

export default {
    name: "alterBankCard",
    data() {
        return {
            alterBankParams: {
                bankconact: "",
                bankaccount: "",
                bankname: ""
            }
        };
    },
    created() {
        if(this.$route.params.bankInfo) {
            this.alterBankParams = this.$route.params.bankInfo;
        }
    },
    methods: {
        /*   */
        getAlterBank(params) {
            reqAlterBank(params).then(res => {
                if (res.data.status === 0) {
                    Toast.success(res.data.msg);
                } else {
                    Toast.fail(res.data.data);
                }
            });
        },

        /*  */
        confirmAlter() {
            Dialog.confirm({
                title: "",
                message: ""
            })
                .then(() => {
                    this.getAlterBank(this.alterBankParams);
                })
                .catch(() => {});
        },
        /*  */
        linkBack() {
            this.$router.back();
        }
    }
};
</script>

<style lang="less" scoped>
.alterBankCard {
    padding-top: 45px;
    box-sizing: border-box;
    background-color: rgb(240, 239, 245);
    height: 100%;
    .header {
        padding: 18px;
        color: rgb(105, 104, 109);
        font-size: 16px;
    }
    .footer {
        text-align: center;
        margin-top: 40px;
        .carryCashBtn {
            width: 70%;
            border-radius: 22px;
            color: #fff;
            background-image: linear-gradient(
                to right,
                rgb(247, 177, 124),
                rgb(247, 212, 172) ;
            );
        }
    }
}
// border: 1px solid #0ff;
</style>


