<template>
    <div class="confirmDelivery">
        <van-nav-bar fixed title="" left-text="" left-arrow @click-left="linkBack" />
        <header class="header"> 2019.11.9</header>
        <aside class="aside">
            <div class="left">
                <img src="../../../assets/icon_todo_list.png" />
                 
            </div>
        </aside>
        <ul class="products">
            <li class="product_item" v-for="(item) in stockInfo" :key="item.id">
                <section class="container">
                    <p class="order_describe">{{ item.ProductName }}</p>
                    <p class="order_number">: {{ item.total + item.Unit }}</p>
                    <p>: {{ item.price }}</p>
                    <p>: {{ item.amount }}</p>
                </section>
                <van-stepper
                    class="stepper"
                    :disabled="status != 15"
                    v-model="item.mayGet"
                    integer
                    min="0"
                    :max="item.mayGetMax"
                />
            </li>
        </ul>

        <!-- <article class="peoduct_info">
            <p class="text">
                20:00,
                :20: 00,
                ;24
                
            </p>
            <h3 class="describe"></h3>
            <div class="container">
                <span>500g2</span>
                <span>1</span>
            </div>
        </article>
 -->
        <footer class="footer">
            <p></p>
            <van-button
                v-if="status == 15"
                class="sunmit_info"
                size="small"
                @click="sunmit_info"
            ></van-button>
        </footer>
    </div>
</template>

<script>
import { Toast } from "vant";
import { reqStockInfo, reqReceipt } from "@/api/orderExpress";
export default {
    name: "reqStockInfo",
    data() {
        return {
            stockInfo: [], // 
            id: -1, // ID
            status: -1 //   status==15
        };
    },
    created() {
        console.log("params >>");
        console.log(this.$route);
        this.id = this.$route.query.id;
        this.status = this.$route.query.status;
        //  
        this.getStockInfo(this.id);
    },
    methods: {
        /*   */
        getStockInfo(params) {
            reqStockInfo(params).then(res => {
                if (res.data.status === 0) {
                    let values = res.data.data.list;
                    console.log(">>>");
                    console.log(values);
                    values.forEach(item => {
                        item.mayGet = item.total - item.Actual; // mayGet ==> 
                        item.mayGetMax = item.total - item.Actual;
                    })
                    this.stockInfo = res.data.data.list;
                } else {
                    console.error(":" + res.data.msg);
                }
            });
        },

        /*  */
        sunmit_info() {
            // 
            let toast = Toast.loading({
                message: "...",
                forbidClick: true
            });

            let list = [];
            this.stockInfo.forEach(item => {
                list.push({
                    id: item.id,
                    value: item.total
                });
            });
            // 
            let params = {
                id: this.id,
                list: JSON.stringify(list)
            };

            //  
            reqReceipt(params).then(res => {
                if (res.data.status === 0) {
                    toast.clear();
                    Toast.success('');
                    this.$router.back();
                } else {
                    Toast.fail(res.data.msg);
                    console.error(":" + res.data.msg);
                }
            });
        },
        /*  */
        linkBack() {
            this.$router.back();
        }
    }
};
</script>

<style lang="less" scoped>
.confirmDelivery {
    padding-top: 45px;
    box-sizing: border-box;
    background-color: rgb(240, 239, 245);
    height: 100%;
    .header {
        height: 45px;
        background-color: #fff;
        border-radius: 5px;
        margin: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 18px;
        color: rgb(50, 50, 50);
    }
    .aside {
        height: 35px;
        background-color: #fff;
        border-radius: 5px;
        margin: 10px;
        padding: 0 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: rgb(120, 120, 120);
        font-size: 12px;
        .left {
            display: flex;
            align-items: center;
            img {
                width: 12px;
                height: 14px;
                margin-right: 6px;
            }
        }
    }
    .products {
        .product_item {
            background-color: #fff;
            border-radius: 5px;
            margin: 10px;
            padding: 0 10px;
            height: 100px;
            display: flex;
            align-items: center;
            position: relative;
            .container {
                height: 100%;
                flex: 3.5;
                display: flex;
                flex-direction: column;
                justify-content: center;
                color: rgb(120, 120, 120);
                font-size: 14px;
            }
            .stepper {
                position: absolute;
                right: 10px;
                bottom: 10px;
            }
        }
    }
    .peoduct_info {
        background-color: #fff;
        border-radius: 5px;
        margin: 10px;
        padding: 10px;
        .text {
            color: rgb(120, 120, 120);
            font-size: 12px;
        }
        .describe {
            font-size: 20px;
            font-weight: normal;
            padding: 3px;
            margin: 7px 0;
            border-top: 1px solid rgb(240, 239, 244);
            border-bottom: 1px solid rgb(240, 239, 244);
        }
        .container {
            color: rgb(120, 120, 120);
            display: flex;
            justify-content: space-between;
            font-size: 14px;
        }
    }
    .footer {
        color: rgb(120, 120, 120);
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
        .sunmit_info {
            background-color: rgb(221, 209, 167);
            border-radius: 15px;
            padding: 0 20px;
            margin: 10px 0;
            color: #fff;
            font-size: 20px;
        }
    }
}
// border: 1px solid #0ff;
</style>


