import { ajax } from "../units/server/ajax";

/*  */
export const reqCarryCash = (amount) => ajax("post", {
    act: "f",
    cmd: "cash",
    amount
})


/*  */
export const reqBankInfo = () => ajax("post", {
    act: "base",
    cmd: "getbank"
})


/*  */
export const reqAlterBank = (data) => ajax("post", {
    act: "base",
    cmd: "savebank",
    ...data
})


