import { ajax } from "../units/server/ajax";

/*  */
export const reqMyStock = (data) => ajax("post", {
    act: "order",
    cmd: "mystock",
    ...data,
    status: 0,
    issub: 1
})


/*   */
export const reqStockInfo = (id) => ajax("post", {
    act: "order",
    cmd: "stockinfo",
    id
})


/*    */
export const reqReceipt = (data) => ajax("post", {
    act: "order",
    cmd: "receipt",
    ...data
})


