import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/page/Login/Login'; // 
import home from '@/page/home/home'; // 
import myAccount from '@/page/myAccount/myAccount'; // 
import myEarnings from '@/page/myEarnings/myEarnings'; // 
import carryCash from '@/page/carryCash/carryCash'; // 
import carryCashSuccess from '@/page/carryCash/carryCashSuccess/carryCashSuccess'; // 
import alterBankCard from '@/page/carryCash/alterBankCard/alterBankCard'; // 
import fullStatus from '@/page/fullStatus/fullStatus'; //
import areaOrder from '@/page/areaOrder/areaOrder'; //
import orderExpress from '@/page/orderExpress/orderExpress'; //
import confirmDelivery from '@/page/orderExpress/confirmDelivery/confirmDelivery'; // 
import clientGetCargo from '@/page/clientGetCargo/clientGetCargo'; // 
import myOrder from '@/page/myOrder/myOrder'; // 
import discountCoupon from '@/page/discountCoupon/discountCoupon'; // 
import myAgreement from '@/page/myAgreement/myAgreement'; // 

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/home/myAccount',
      name: 'home_myAccount',
      component: myAccount
    },
    {
      path: '/home/myEarnings',
      name: 'home_myEarnings',
      component: myEarnings
    },
    {
      path: '/home/carryCash',
      name: 'home_carryCash',
      component: carryCash
    },
    {
      path: '/home/carryCash/carryCashSuccess',
      name: 'home_carryCash_carryCashSuccess',
      component: carryCashSuccess
    },
    {
      path: '/home/carryCash/alterBankCard',
      name: 'home_carryCash_alterBankCard',
      component: alterBankCard
    },
    {
      path: '/home/fullStatus',
      name: 'home_fullStatus',
      component: fullStatus
    },
    {
      path: '/home/areaOrder',
      name: 'home_areaOrder',
      component: areaOrder
    },
    {
      path: '/home/orderExpress',
      name: 'home_orderExpress',
      component: orderExpress
    },
    {
      path: '/home/orderExpress/confirmDelivery',
      name: 'home_orderExpress_confirmDelivery',
      component: confirmDelivery
    },
    {
      path: '/home/clientGetCargo',
      name: 'home_clientGetCargo',
      component: clientGetCargo
    },
    {
      path: '/home/myOrder',
      name: 'home_myOrder',
      component: myOrder
    },
    {
      path: '/home/discountCoupon',
      name: 'home_discountCoupon',
      component: discountCoupon
    },
    {
      path: '/home/myAgreement',
      name: 'home_myAgreement',
      component: myAgreement
    },
    {
      path: '*',
      component: Login
    }
  ]
})



