<template>
    <div class="AwaitInCargo">
        <van-search
            v-model="getOrderDataParmas.contact"
            placeholder=""
            show-action
            shape="round"
        >
            <div slot="action" @click="onSearch"></div>
        </van-search>

        <van-list
            class="data"
            v-model="listLoading"
            :finished="finished"
            finished-text=""
            @load="onLoad"
        >
            <van-cell class="li" v-for="item in orderData" :key="item.id">
                <header class="header">
                    <div>{{ item.amount}}</div>
                    <div>{{ item.id }}</div>
                </header>
                <section class="item" v-for="item_sub in item.detaileList" :key="item_sub.id">
                    <img class="item_main_img" :src="item_sub.mainImg" />
                    <section class="container">
                        <p class="product_name">{{ item_sub.pname }}</p>
                        <p class="product_number">{{ item_sub.number + item_sub.unit }}</p>
                        <p class="product_price">{{ item_sub.pcice }}</p>
                    </section>
                </section>
                <footer class="get_cargo_address">
                    <p>{{ item.DateTimePay }}</p>
                    <p>{{ item.GetProductAddress }} {{ item.consignee }}</p>
                </footer>
                <van-button
                    v-if="item.States == 15"
                    :listLoading="btnLoading"
                    type="info"
                    size="mini"
                    class="get_cargo_btn"
                    @click="confirmGetCargo(item.id)"
                ></van-button>
            </van-cell>
        </van-list>
    </div>
</template>

<script>
import { Toast } from "vant";
import { reqOrderData, reqGetCargo } from "@/api/myOrder";

const pagesize = 10;
// 
let getOrderDataParmas = { pageindex: 1, pagesize, status: 15 }; // 

export default {
    name: "AwaitInCargo",
    data() {
        return {
            finished: false, // 
            btnLoading: false, //
            orderData: [], // 
            getOrderDataParmas,
            orderDataTotal: 1, // 
            listLoading: false, // 
            activeId: -1, // 
        };
    },
    created() {
        this.getOrderData(this.getOrderDataParmas);
    },
    methods: {
        onLoad() {
            // 
            if (this.orderData.length >= this.orderDataTotal && this.getOrderDataParmas.pageindex > this.orderDataTotal) {
                this.finished = true;
                return;
            }
            this.getOrderDataParmas.pageindex += pagesize;
            //  
            this.getOrderData(this.getOrderDataParmas);
            console.count(" >>>>");
        },
        /*   */
        getOrderData(params) {
            reqOrderData(params).then(res => {
                if (res.data.status === 0) {
                    if (this.orderData.length <= 0) {
                        this.orderData = res.data.data.list;
                        this.orderDataTotal = res.data.data.recordcount;
                    } else {
                        res.data.data.list.forEach(item => {
                            this.orderData.push(item);
                        });
                    }
                    
                    // 
                    this.listLoading = false;
                } else {
                    console.error(":" + res.data.msg);
                }
            });
        },
         /*  */
        onSearch() {
            this.orderData = [];
            this.getOrderDataParmas.pageindex = 1;
            this.getOrderData(this.getOrderDataParmas);
        },
        /*  */
        confirmGetCargo(oid) {
            // 
            if(this.btnLoading && this.activeId == oid) {
                return;
            }
            this.activeId = oid;
            this.btnLoading = true;
            reqGetCargo(oid).then(res => {
                if (res.data.status === 0) {
                    // 
                    this.orderData = [];
                    // 
                   this.getOrderData(this.getOrderDataParmas);
                    Toast.success(res.data.msg);
                    this.btnLoading = false;
                } else {
                    Toast.fail(res.data.msg);
                }
            });
        }
    }
};
</script>

<style lang="less" scoped>
.AwaitInCargo {
    background-color: rgb(240,239,245);
    .data {
        display: flex;
        flex-direction: column;
        padding: 10px;
        .li {
            padding: 0;
            background-color: #fff;
            margin-bottom: 10px;
            border-radius: 5px;
            position: relative;
            .header{
                display: flex;
                justify-content: space-between;
                padding: 5px 15px 0 15px;
                font-size: 12px;
                color: #949494;
            }
            .item {
                height: 100px;
                display: flex;
                align-items: center;
                border-bottom: 1px solid rgb(240,239,245);
                .item_main_img {
                    width: 80px;
                    height: 80px;
                    margin: 0 20px;
                    border-radius: 5px;
                }
                .container {
                    height: 100%;
                    flex: 3.5;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    color: rgb(120, 120, 120);
                    font-size: 14px;
                    .product_name {
                        font-weight: normal;
                    }
                    .product_number {
                        margin: 10px 0;
                    }
                    .product_price {
                        font-size: 14px;
                    }
                }
            }
            .get_cargo_address {
                margin: 0 20px 10px 20px;
                color: rgb(120, 120, 120);
                font-size: 12px;
            }
        }
    }
    .get_cargo_btn {
        position: absolute;
        right: 10px;
        bottom: 70px;
    }
}
// border: 1px solid #0ff;
</style>


